<template>
  <div>
    <v-toolbar flat class="primary">
      <v-spacer></v-spacer>
      <v-img
        :src="
          mode
            ? require('../../assets/image/switch_list_btn.svg')
            : require('../../assets/image/switch_node_btn.svg')
        "
        max-height="60"
        max-width="120"
        class="switch_mode_image"
        @click="mode = !mode"
      ></v-img>
    </v-toolbar>
    <gateway-list :gatewayinfo="gatewayinfo" v-if="mode" />
    <gateway-node :gatewayinfo="gatewayinfo" v-else />
  </div>
</template>
<script>
import GatewayList from "./GatewayList.vue";
import GatewayNode from "./GatewayNode.vue";
export default {
  props: ["gateway_data"],
  watch: {
    gateway_data() {
      this.onBandNumHandler(this.gateway_data);
    },
  },
  components: {
    GatewayList,
    GatewayNode,
  },
  created() {},
  async mounted() {
    console.log("gateway");
    if (this.$session.user != null) {
      await this.getGatewayList();
    }
  },
  methods: {
    onBandNumHandler(data) {
      console.log("onBandNumHandler");

      var index = this.gatewayinfo
        .map(function(g) {
          return g["pid"];
        })
        .indexOf(data.panid);
      if (index !== -1) {
        console.log(data);
        this.gatewayinfo[index].cbandnum = data.bandnum;
        this.gatewayinfo[index].connect_state = data.connectstate;
      }
    },
    async getGatewayList() {
      console.log("getGatewayList");
      try {
        await this.$http.get("gateway/bandnum");
        var gateway = await this.$http.get(
          "users/gatewayinfo/" + this.$session.user.id
        );
        gateway = gateway.data.data;

        for (var g = 0; g < gateway.length; g++) {
          var bandnum = await this.$http.get(
            "gatewaysbands/num/" + gateway[g].id
          );
          bandnum = bandnum.data.data;

          this.gatewayinfo.push({
            id: gateway[g].id,
            pid: gateway[g].pid,
            alias: gateway[g].alias,
            created: gateway[g].created,
            ip: gateway[g].ip,
            lat: gateway[g].lat,
            lng: gateway[g].lng,
            disconnect_time: gateway[g].disconnect_time,
            connect_time: gateway[g].connect_time,
            connect_state: false,
            bandnum: bandnum,
            cbandnum: 0,
          });
        }
      } catch {
        this.gatewayinfo = [];
      }
    },
  },
  data: () => ({
    restapi: false,
    mode: true,
    gatewayinfo: [],
  }),
};
</script>
